.section__padding1 {
  max-width: 1400px;
  padding: 2rem 8rem;
}
.blog-sec-one {
  display: flex;
}
.blog-sec-two {
  display: flex;
}

.blog-sec-three {
  width: 100%;
  flex: 1;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2rem;
  align-items: baseline;
}

.blog-read-lg {
  min-width: 740px;
}
.blog-sec-one div {
  margin: 0rem 0.5rem;
}
.blog-sec-two div {
  margin: 0rem 0.5rem;
}

.blog_section-content h1 {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 19px;
  line-height: 25px;
  margin: 1rem 0;
  color: #060056;
}

.blog_section-content p {
  color: var(--color-text);
  font-family: var(--font-family2);
  font-weight: 200;
  font-size: 16px;
  line-height: 25px;
  margin: 1rem 0;
}
.blog_section-content button {
  width: 170px;
  height: 40px;
  font-family: Archivo;
  font-size: 15px;
  font-weight: bold;
  text-align: center;
  color: #060056;
  border-radius: 40px;
  border: 1px solid #060056;
  box-shadow: 0px 0px 1px 0px #060056;
  cursor: pointer;
}

.Familiarization {
  color: #060056 !important;
  font-family: var(--font-family) !important;
  font-size: 20px;
  margin: 0 !important;
  font-weight: 500 !important;
}
.bimg-cont {
  display: flex;
  justify-content: flex-start;
  width: 100%;
}
.bimg-cont img {
  max-width: 22rem;
}
.bimg-cont-lg {
  display: flex;
  justify-content: flex-start;
  width: 100%;
}
.bimg-cont-lg img {
  max-width: 40rem;
}
.blog-img-cont {
  flex: 1 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;

  padding: 4rem;
}

.blog-img-cont img {
  max-width: 30rem;
}
@media screen and (max-width: 1200px) {
  .bimg-cont-lg img {
    max-width: 30rem;
  }
  .blog-sec-one {
    flex-direction: column;
  }
  .blog-sec-two {
    flex-direction: column;
  }
  .blog-sec-one div {
    margin: 1rem 0rem;
    justify-content: center;
  }
  .blog-sec-two div {
    margin: 1rem 0rem;
    justify-content: center;
  }
  .blog-read-lg {
    min-width: auto;
  }
  .blog-img-cont {
    justify-content: center;
    padding: 1rem;
  }
  .blog-sec-three {
    flex-direction: column;

    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 700px) {
  .bimg-cont-lg img {
    max-width: 19rem;
  }
  .blog-img-cont img {
    max-width: 20rem;
  }
  .blog-img-cont {
    justify-content: center;
    padding: 1rem;
  }
  .section__padding1 {
    padding: 2rem 2rem;
  }
  .bimg-cont {
    justify-content: center;
  }
  .blog_section-content p {
    font-size: 16px;
    margin: 1rem 0rem;
  }

  .blog_section-content h1 {
    font-size: 20px;
    margin: 0;
    line-height: 30px;
  }
  .blog-sec-three {
    grid-template-columns: repeat(1, 1fr);
  }
}
